.app__navbar {
  width: 100%;
  height: 82px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}

.app__navbar-logolink {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app__navbar-logolink_logo {
  flex: 1;
}

.app__navbar-logolink_logo img {
  width: 147px;
  height: 67px;
  background: var(--color-black);
}

.app__navbar-logolink_links {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3rem;
}

.app__navbar-logolink_links li {
  margin: 0 1rem;
  transition: 0.5s ease;
}

.app__navbar-logolink_links li:hover {
  color: var(--color-gray);
}

.app__navbar-search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__navbar-search svg,
.app__navbar-search p {
  margin: 0 1rem;
  cursor: pointer;
}

.app__navbar-search svg:last-child {
  margin-left: 0.5rem;
}

.app__navbar-search img {
  width: 32px;
  height: 32px;
  margin-left: 1rem;
}
